import { Component, OnInit, Renderer2 } from '@angular/core';
import {AllfeatureService} from '../../shared/allfeature.service';
import { map } from 'rxjs/operators';
import { Allfeatures } from '../../features/all-feature.model';
@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.scss']
})
export class FaqComponent implements OnInit {
  faq: any;
  faqbody = false ; faqbody1 = false ; faqbody2 = false ;
  constructor(private faqdata: AllfeatureService , private render: Renderer2) {
    this.faqdata.getfaqdata().subscribe(
      (data: any) => {
      this.faq = data;
      }
    );
  }

  ngOnInit() {
  }

showcontent(e) {
console.log(e.target);
if (e.target.nextElementSibling.classList.contains('showbody')) {
  this.render.removeClass(e.target.nextElementSibling, 'showbody');
  this.render.removeClass(e.target, 'active');
} else {
  this.render.addClass(e.target.nextElementSibling , 'showbody');
  this.render.addClass(e.target , 'active');
}


}

}
