import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Allfeatures } from '../features/all-feature.model';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AllfeatureService {

  constructor(private http: HttpClient) { }

  path = 'assets/all-features.json';
  pathmanage = 'assets/manage-feature.json';
  faqpath = 'assets/faq-data.json';

  getFeature(): Observable<Allfeatures[]> {
    return this.http.get<Allfeatures[]>(this.path) ;
  }

  getManageFeature(): Observable<Allfeatures[]> {
    return this.http.get<Allfeatures[]>(this.pathmanage) ;
  }

  getfaqdata(): Observable<Allfeatures[]> {
    return this.http.get<Allfeatures[]>(this.faqpath) ;
  }
}
