import { Component, OnInit, ViewChild, ComponentFactoryResolver, ViewContainerRef } from '@angular/core';
import { LoginDirective } from '../loginindicator.directive';
import { LoginAlertComponent} from '../loginalert/loginalert.component';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-logininnerpage',
  templateUrl: './logininnerpage.component.html',
  styleUrls: ['./logininnerpage.component.scss']
})
export class LogininnerpageComponent implements OnInit {
   private closesub = [];

   @ViewChild(LoginDirective , {read : ViewContainerRef} ) loginalert: ViewContainerRef;
  constructor(private generatecomponentfactory: ComponentFactoryResolver) { }

  ngOnInit() {
  this.createone();
  }
 createone() {
    const getcomponentfactory = this.generatecomponentfactory.resolveComponentFactory( LoginAlertComponent );
    const viewcontainerref = this.loginalert;
    // viewcontainerref.clear();
    const componentRef = viewcontainerref.createComponent(getcomponentfactory);
    componentRef.instance.massage = 'You have logged in successfully!';
    componentRef.instance.accessparent = this;
    this.closesub.push(componentRef);
}

removeone() {
  // to access all the dynamic component from the array
  // let componentRef = this.componentsReferences.filter(x => x.instance.index == index)[0];
  const component = this.closesub[0];
  this.loginalert.remove(1);
}

}
