import {Component, OnInit} from '@angular/core';
import {Allfeatures} from '../all-feature.model';
import {AllfeatureService} from '../../shared/allfeature.service';
import {from, range, of} from 'rxjs';
import {take, map} from 'rxjs/operators';
@Component({selector: 'app-all-feature', templateUrl: './all-feature.component.html', styleUrls: ['./all-feature.component.scss']})
export class AllFeatureComponent implements OnInit {

  moreview = false;
  moreview2 = false;

  printdata: Allfeatures[] = [];

  firstprint: Allfeatures[] = [];
  secondprint: Allfeatures[] = [];
  thirdprint: Allfeatures[] = [];
  fourthprint: Allfeatures[] = [];
  allfeaturedata: any;

  constructor(private allfeature: AllfeatureService) {
    this
      .allfeature
      .getFeature()
      .pipe(map(data => {
        const arr = [];
        for (const obj of data) {
          if (obj) {
            arr.push(obj);
          }
        }
        return arr;
      }))
      .subscribe((data: Allfeatures[]) => {
        this.printdata = data;
        for ( let i = 0 ; i < 6; i++) {
          this.firstprint.push(data[i]);
        }
        for ( let i = 6 ; i  < 12 ; i++) {
          this.secondprint.push(data[i]);
        }
        for ( let i = 12 ;  i < 18 ; i++) {
          this.thirdprint.push(data[i]);
        }
        for ( let i = 18 ; i < 22 ; i++) {
          this.fourthprint.push(data[i]);
        }
        console.log(this.printdata);
      });


  }

  viewmore() {
    this.moreview = !this.moreview;
  }
  viewmore2() {
    this.moreview2 = !this.moreview2;
  }

  ngOnInit() {}
}
