import {Component, OnInit} from '@angular/core';
import {AllfeatureService} from '../../shared/allfeature.service';
import {Allfeatures} from '../all-feature.model';
import {map} from 'rxjs/operators';

@Component({selector: 'app-manage-feature', templateUrl: './manage-feature.component.html', styleUrls: ['./manage-feature.component.scss']})
export class ManageFeatureComponent implements OnInit {

  featurelistmain: Allfeatures[] = [];
  featurelist1: Allfeatures[] = [];
  featurelist2: Allfeatures[] = [];
  showpoto = false;

  constructor(private managefeatureservice: AllfeatureService) {
    this
      .managefeatureservice
      .getManageFeature()
      .pipe(map(data => {
        const arr = [];
        for (const obj of data) {
          if (obj) {
            arr.push(obj);
          }
        }
        return arr;
      }))
      .subscribe((data: Allfeatures[]) => {
        this.featurelistmain = data;

        for (let i = 0; i < 6; i++) {
          this
            .featurelist1
            .push(data[i]);
        }

        for (let i = 6; i < 11; i++) {
          this
            .featurelist2
            .push(data[i]);
        }
        console.log(this.featurelist1, this.featurelist2);
      });

    setInterval(() => {
      this.showpoto = !this.showpoto;
    }, 3000);
  }

  ngOnInit() {}
}
